@import 'mixins';

.container {
    height: 100vh;
    background-color: #229954;
    padding: 1rem;
    @include scroll-bar;
    text-align: center;
    .header {
        h1 {
            color: #fff; } }
    .table {
        margin: 1rem;
        margin-bottom: 3rem; }
    .text__headers {
        color: white;
        font-size: 1rem;
        font-family: roboto;
        span {
            font-size: 1.1rem;
            font-weight: bold; } } }
